/* scheme */

/* 
.dark-primary-color    { background: #512DA8; }
.default-primary-color { background: #673AB7; }
.light-primary-color   { background: #D1C4E9; }
.text-primary-color    { color: #FFFFFF; }
.accent-color          { background: #38ef7d; }
.primary-text-color    { color: #212121; }
.secondary-text-color  { color: #757575; }
.divider-color         { border-color: #BDBDBD; }
 */


:root {
  --text_primary: #212121; /* обычный текст */
  --accent: #38ef7d; /* text area border */
  --button_primary_background: #673AB7; /* primary button */
  --button_primary_foreground: #FFFFFF; /* primary button text */
  --button_secondary_background: #38ef7d; /* secondary button */
  --button_secondary_foreground: #212121; /* secondaty button text */
  --button_tertiary_background: #D1C4E9;  /* tertiary button */
  --button_tertiary_foreground: #212121;  /* tertiary button text */
  --header_text: #212121;
  --header_text_alternate: #212121;
  --header_text_secondary: #818c99;
  --header_tint: #212121; /* header controls */
  --text_secondary: #757575; /* sub-texts */
  --background_page: #ebedf0; /* standart vk backround*/
  --button_outline_border: #673AB7;
  --button_outline_foreground: #673AB7;
}

